import './App.css';
import { Routes, Route } from 'react-router-dom';
// import routes from './pages/index'
import { useState } from 'react';

import Home01 from './pages/Home01';
import ItemDetails01 from './pages/ItemDetails01';


function App() {

    const localselectedItem = localStorage.getItem('setSelectedCard') ? JSON.parse(localStorage.getItem('setSelectedCard')) : null;
    const [selectedCard, setSelectedCard] = useState(localselectedItem)
    console.log(selectedCard)
    
    const handleSetSelectedCard = (params) => {
        setSelectedCard(params)
        localStorage.setItem('setSelectedCard', JSON.stringify(params))
    }


    return (
        <Routes >
            {/* {
                routes.map((data, index) => (
                    <Route
                        onUpdate={() => window.scrollTo(0, 0)}
                        exact={true} path={data.path}
                        element={data.component} key={index}
                        
                    />
                ))
            } */}

            <Route path="" element={<Home01 selectedCard={selectedCard} handleSetSelectedCard={handleSetSelectedCard}/>} />
            <Route path="item-details" element={<ItemDetails01 selectedCard={selectedCard} handleSetSelectedCard={handleSetSelectedCard} />} />
        </Routes>
    );
}

export default App;
