import React, { useState, Fragment } from 'react';
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'
import CardModal from './CardModal';

import QuantityImg from './../../assets/inspirest/quantity.png'
import { useMediaQuery } from '../../hooks/useMediaQuery';

const TodayPicks = ({ selectedCard, handleSetSelectedCard, data }) => {

    const isRowBased = useMediaQuery('(min-width: 300px)');

    const [visible, setVisible] = useState(8);
    const showMoreItems = () => {
        setVisible((prevValue) => prevValue + 4);
    }
    const [modalShow, setModalShow] = useState(false);
    return (
        <Fragment>

            <section className="tf-section today-pick">
                <div className="themesflat-container">
                    <div className="row">


                        {
                            data.slice(0, visible).map((item, index) => (
                                <div
                                    onClick={() => handleSetSelectedCard(item)}
                                    key={index} className="col-xl-3 col-lg-4 col-md-6 col-sm-6">
                                    <div className={`sc-card-product ${item.feature ? 'comingsoon' : ''} `}>

                                        <div className="card-media">
                                            <Link to="/item-details">
                                                <img src={item.img} alt="axies" />
                                            </Link>
                                            <Link to="" className="wishlist-button">
                                                <img src={QuantityImg} alt="Axies" style={{
                                                    width: '15px',
                                                    height: '15px',
                                                    marginRight: '5px',
                                                }} />
                                                <span className="number-like">
                                                    {item.quantity}
                                                </span>
                                            </Link>
                                            <div className="coming-soon">{item.feature}</div>
                                        </div>

                                        <div className="card-title">
                                            <h5 className="style2"><Link to="">{item.title}</Link></h5>
                                            <div className="tags"
                                                style={{
                                                    width: 'auto',
                                                    padding: '5px',
                                                    display: 'flex',
                                                    alignItems: 'center',
                                                    justifyContent: 'center',
                                                }}
                                            >{item.tags}</div>
                                        </div>

                                        <div 
                                        
                                        className="meta-info"
                                        style={{
                                            display: "flex",
                                            flexDirection: !isRowBased ? 'column' : 'row',
                                        }}
                                        >
                                            
                                            
                                            <button
                                                className="sc-button style bag fl-button pri-3 no-bg"
                                                onClick={() => {
                                                    setModalShow(true)
                                                    handleSetSelectedCard(item)
                                                }}
                                                style={{
                                                    whiteSpace: 'nowrap',
                                                }}
                                            >
                                                <span>Place Bid</span>
                                            </button>

                                            <div className="price">
                                                <span>Current Bid</span>
                                                <h5> {item.price}</h5>
                                            </div>

                                        </div>


                                    </div>

                                </div>
                            ))
                        }


                    </div>
                </div>
            </section>

            <CardModal
                show={modalShow}
                onHide={() => setModalShow(false)}
                selectedCard={selectedCard}
            />

        </Fragment>
    );
}



TodayPicks.propTypes = {
    data: PropTypes.array.isRequired,
}


export default TodayPicks;
